"use client";
import { useState, useEffect } from 'react';
import { auth, googleProvider } from '../firebase';
import { signInWithPopup, signOut, onAuthStateChanged, createUserWithEmailAndPassword, signInWithEmailAndPassword } from 'firebase/auth';
import Link from 'next/link';
import { FaGoogle, FaUserGraduate, FaTimes } from 'react-icons/fa';

export default function Navbar() {
  const [user, setUser] = useState(null);
  const [showModal, setShowModal] = useState(false);
  const [isSignUp, setIsSignUp] = useState(false);
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [error, setError] = useState('');

  // 1. Monitor Auth State
  useEffect(() => {
    const unsubscribe = onAuthStateChanged(auth, (currentUser) => {
      setUser(currentUser);
      if (currentUser) setShowModal(false); // Close modal on success
    });
    return () => unsubscribe();
  }, []);

  // 2. Auth Handlers
  const handleGoogleLogin = async () => {
    try { await signInWithPopup(auth, googleProvider); } catch (e) { setError(e.message); }
  };

  const handleEmailAuth = async (e) => {
    e.preventDefault();
    setError('');
    try {
      if (isSignUp) {
        await createUserWithEmailAndPassword(auth, email, password);
      } else {
        await signInWithEmailAndPassword(auth, email, password);
      }
    } catch (err) {
      setError(err.message.replace("Firebase:", ""));
    }
  };

  return (
    <>
      <nav className="bg-white/80 backdrop-blur-md border-b border-slate-200 sticky top-0 z-40 h-16 px-6 flex items-center justify-between shadow-sm">
        {/* Logo */}
        <Link href="/" className="flex items-center gap-2">
            <div className="w-8 h-8 bg-teal-700 text-white flex items-center justify-center rounded font-bold text-lg">V</div>
            <span className="font-bold text-xl text-slate-900 tracking-tight">VIRULENCE <span className="text-teal-600">EDU</span></span>
        </Link>

        {/* Desktop Menu */}
        <div className="flex items-center gap-6">
          <Link href="/" className="hidden md:block text-sm font-medium hover:text-teal-600">Home</Link>
          <Link href="/#courses" className="hidden md:block text-sm font-medium hover:text-teal-600">Courses</Link>
          <Link href="/#contact" className="hidden md:block text-sm font-medium hover:text-teal-600">Contact</Link>

          {/* ADMIN LINK - STRICTLY RESTRICTED */}
          {user?.email === 'contact@kamarjahan.in' && (
            <Link href="/admin" className="bg-red-50 text-red-600 border border-red-200 px-3 py-1 rounded text-xs font-bold uppercase tracking-wider hover:bg-red-100">
              Admin Panel
            </Link>
          )}

          {user ? (
            <div className="flex items-center gap-4 pl-4 border-l">
               <div className="text-right hidden md:block">
                 <p className="text-xs font-bold text-slate-900">{user.displayName || user.email}</p>
                 <p className="text-[10px] text-slate-500 uppercase">Student ID: {user.uid.slice(0,6)}</p>
               </div>
               <button onClick={() => signOut(auth)} className="text-sm text-red-500 hover:text-red-700 font-medium">Log Out</button>
            </div>
          ) : (
            <button onClick={() => setShowModal(true)} className="flex items-center gap-2 bg-slate-900 hover:bg-slate-800 text-white px-5 py-2 rounded-full text-sm font-bold transition">
              <FaUserGraduate /> Student Login
            </button>
          )}
        </div>
      </nav>

      {/* LOGIN / SIGNUP MODAL */}
      {showModal && (
        <div className="fixed inset-0 z-50 bg-black/60 flex items-center justify-center p-4 backdrop-blur-sm">
          <div className="bg-white rounded-2xl shadow-2xl w-full max-w-md overflow-hidden relative">
            <button onClick={() => setShowModal(false)} className="absolute top-4 right-4 text-slate-400 hover:text-slate-600"><FaTimes /></button>
            
            <div className="p-8">
              <h2 className="text-2xl font-bold text-center text-slate-900 mb-2">{isSignUp ? 'Create Account' : 'Welcome Back'}</h2>
              <p className="text-center text-slate-500 text-sm mb-6">Access your Virulence Dashboard</p>

              {/* Google Button */}
              <button onClick={handleGoogleLogin} className="w-full flex items-center justify-center gap-2 border border-slate-300 p-3 rounded-lg hover:bg-slate-50 font-medium text-slate-700 transition mb-4">
                <FaGoogle className="text-red-500" /> Continue with Google
              </button>

              <div className="relative my-6 text-center text-xs text-slate-400 uppercase">
                <span className="bg-white px-2 relative z-10">Or using email</span>
                <div className="absolute top-1/2 left-0 w-full border-t border-slate-200 -z-0"></div>
              </div>

              {/* Email Form */}
              <form onSubmit={handleEmailAuth} className="space-y-4">
                <input type="email" placeholder="Email Address" required value={email} onChange={e => setEmail(e.target.value)} 
                       className="w-full p-3 bg-slate-50 border border-slate-200 rounded-lg focus:outline-none focus:border-teal-500 transition" />
                <input type="password" placeholder="Password" required value={password} onChange={e => setPassword(e.target.value)} 
                       className="w-full p-3 bg-slate-50 border border-slate-200 rounded-lg focus:outline-none focus:border-teal-500 transition" />
                
                {error && <p className="text-red-500 text-xs text-center bg-red-50 p-2 rounded">{error}</p>}
                
                <button type="submit" className="w-full bg-teal-600 hover:bg-teal-700 text-white p-3 rounded-lg font-bold shadow-lg shadow-teal-600/20 transition">
                  {isSignUp ? 'Register Student' : 'Login Securely'}
                </button>
              </form>

              <div className="mt-6 text-center text-sm">
                <button onClick={() => setIsSignUp(!isSignUp)} className="text-teal-600 hover:underline font-medium">
                  {isSignUp ? 'Already have an account? Login' : 'New student? Create Account'}
                </button>
              </div>
            </div>
            <div className="bg-slate-50 p-4 text-center text-xs text-slate-400 border-t">
              Secure Authentication powered by Google Firebase
            </div>
          </div>
        </div>
      )}
    </>
  );
}