"use client";
import { useState, useEffect } from 'react';
import { db, auth } from '../../firebase';
import { collection, addDoc, getDocs, deleteDoc, doc } from 'firebase/firestore';
import { onAuthStateChanged } from 'firebase/auth';
import { useRouter } from 'next/navigation';
import Navbar from '../../components/Navbar';
import { FaTrash, FaPlus } from 'react-icons/fa';

export default function AdminPanel() {
  const [courses, setCourses] = useState([]);
  const [form, setForm] = useState({ title: '', category: '12th Science', price: '', image: '', desc: '' });
  const [loading, setLoading] = useState(true);
  const [authorized, setAuthorized] = useState(false);
  const router = useRouter();

  // 1. SECURITY CHECK
  useEffect(() => {
    const unsubscribe = onAuthStateChanged(auth, (user) => {
      if (user && user.email === 'contact@kamarjahan.in') {
        setAuthorized(true);
        fetchCourses(); // Only fetch if authorized
      } else {
        setAuthorized(false);
        // Optional: router.push('/'); // Redirect unauthorized users
      }
      setLoading(false);
    });
    return () => unsubscribe();
  }, [router]);

  const fetchCourses = async () => {
    const q = await getDocs(collection(db, "courses"));
    setCourses(q.docs.map(d => ({ ...d.data(), id: d.id })));
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    if(!form.title || !form.price) return alert("Title and Price required");
    await addDoc(collection(db, "courses"), { ...form, createdAt: new Date() });
    setForm({ title: '', category: '12th Science', price: '', image: '', desc: '' });
    fetchCourses();
    alert("Course Added Successfully");
  };

  const handleDelete = async (id) => {
    if(confirm("Are you sure you want to delete this course?")) {
      await deleteDoc(doc(db, "courses", id));
      fetchCourses();
    }
  };

  // Loading State
  if (loading) return <div className="h-screen flex items-center justify-center">Verifying Admin Access...</div>;

  // Unauthorized State
  if (!authorized) return (
    <div className="h-screen flex flex-col items-center justify-center bg-slate-50 p-4 text-center">
      <h1 className="text-4xl font-bold text-red-600 mb-4">Access Denied</h1>
      <p className="text-slate-600 mb-6">You are not authorized to view this page.</p>
      <p className="text-sm text-slate-400">Required: contact@kamarjahan.in</p>
      <a href="/" className="mt-6 text-teal-600 underline">Return to Home</a>
    </div>
  );

  return (
    <div className="min-h-screen bg-slate-50">
      <Navbar />
      <div className="max-w-6xl mx-auto p-6 md:p-12">
        <div className="flex justify-between items-end mb-8">
            <div>
                <h1 className="text-3xl font-bold text-slate-900">Admin Dashboard</h1>
                <p className="text-teal-600 text-sm">Welcome, contact@kamarjahan.in</p>
            </div>
            <div className="text-right">
                <span className="bg-slate-900 text-white px-3 py-1 rounded text-xs">Total Courses: {courses.length}</span>
            </div>
        </div>
        
        <div className="grid lg:grid-cols-3 gap-8">
          {/* FORM SECTION */}
          <div className="bg-white p-6 rounded-xl shadow-sm border border-slate-200 h-fit">
            <h2 className="text-lg font-bold mb-4 flex items-center gap-2"><FaPlus className="text-teal-600"/> Add New Course</h2>
            <form onSubmit={handleSubmit} className="space-y-3">
              <div>
                <label className="text-xs font-bold text-slate-500 uppercase">Course Title</label>
                <input value={form.title} onChange={e => setForm({...form, title: e.target.value})} className="w-full p-2 border rounded text-sm" placeholder="e.g. NEET Biology 2025" />
              </div>
              
              <div className="grid grid-cols-2 gap-2">
                <div>
                    <label className="text-xs font-bold text-slate-500 uppercase">Category</label>
                    <select value={form.category} onChange={e => setForm({...form, category: e.target.value})} className="w-full p-2 border rounded text-sm">
                        <option>10th Science</option>
                        <option>12th Science</option>
                        <option>Entrance</option>
                        <option>Motivation</option>
                    </select>
                </div>
                <div>
                    <label className="text-xs font-bold text-slate-500 uppercase">Price (₹)</label>
                    <input type="number" value={form.price} onChange={e => setForm({...form, price: e.target.value})} className="w-full p-2 border rounded text-sm" />
                </div>
              </div>

              <div>
                <label className="text-xs font-bold text-slate-500 uppercase">Image URL (ImgBB)</label>
                <input value={form.image} onChange={e => setForm({...form, image: e.target.value})} className="w-full p-2 border rounded text-sm" placeholder="https://i.ibb.co/..." />
              </div>

              <div>
                <label className="text-xs font-bold text-slate-500 uppercase">Description</label>
                <textarea value={form.desc} onChange={e => setForm({...form, desc: e.target.value})} className="w-full p-2 border rounded text-sm h-24" placeholder="Course details..."></textarea>
              </div>

              <button className="w-full bg-teal-600 hover:bg-teal-700 text-white py-2 rounded font-bold shadow-lg transition">Upload Course</button>
            </form>
          </div>

          {/* LIST SECTION */}
          <div className="lg:col-span-2 space-y-4">
            {courses.length === 0 && <p className="text-center text-slate-400 py-10">No courses uploaded yet.</p>}
            {courses.map(course => (
              <div key={course.id} className="flex flex-col md:flex-row bg-white p-4 rounded-xl border border-slate-200 shadow-sm items-center gap-4">
                <div className="w-full md:w-24 h-24 bg-slate-100 rounded overflow-hidden shrink-0">
                    {course.image && <img src={course.image} className="w-full h-full object-cover" alt="thumb"/>}
                </div>
                <div className="flex-1 text-center md:text-left">
                  <h3 className="font-bold text-lg text-slate-800">{course.title}</h3>
                  <p className="text-xs text-slate-500 uppercase font-bold tracking-wide">{course.category} • ₹{course.price}</p>
                  <p className="text-sm text-slate-400 line-clamp-1 mt-1">{course.desc}</p>
                </div>
                <button onClick={() => handleDelete(course.id)} className="text-red-500 hover:bg-red-50 p-2 rounded-full transition">
                  <FaTrash />
                </button>
              </div>
            ))}
          </div>
        </div>
      </div>
    </div>
  );
}